*{
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  font-family: 'Mulish', sans-serif;
}
.navbar{
  background: white !important;
}
.navbar-brand{
  color: #565387 !important;
  font-size: 1.9rem !important;
}
.navbar a{
  font-size: 1.3rem !important;
  text-transform: capitalize;
}
.menu_active{
  font-weight: bold;
  border-bottom: 1px solid #565387;
}
.navbar a:hover{
  color: #3498db !important;
}
#header{
  width: 100%;
  height: 80vh;
  margin-top: 70px;
}
#header h1{
  margin: 0 0 10px 0;
  font-size: 48px;
  color: black;
  font-weight: 700;
}
#header .brand-name{
  color: #3498db;
}
#header h2{
  font-size: 24px;
  color: #484848;
}
#header .btn{
  font-size: 16px;
  font-weight: 500;
  display: inline-block;
  padding: 10px 30px;
  transition: .4s;
  color: #3498db;
  letter-spacing: 1px;
  border-radius: 50px;
  text-decoration: none;
  border: 2px solid #3498db;
}
#header .btn:hover{
  color: #fff;
  background: #3498db;
}
#header .animated{
  animation: up-down 1.5s ease-in-out infinite alternate-reverse both;
}
@-webkit-keyframes up-down{
  0%{
  transform: translateY(15px);
  }
  100%{
    transform: translateY(-15px);
  }
}
@keyframes up-down{
  0%{
    transform: translateY(15px);
    }
    100%{
      transform: translateY(-15px);
    }
}